<script>

import EventBus from './utils/EventBus.js';
import mixin from "./mixin/myMixin";
import {gsap, Expo, Power0, Power2, Sine } from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);


export default {
  name: 'About',
  mixins: [mixin], 
  data () {
    return {
      isBoolean: true,
      // headerChangeCheck : true
    }
  },
  
  created(){
     
  },
  mounted() { 
    this.$nextTick(function() {
      this.headerBgCheck();
      this.section1Show();
      this.handelResize();
    
    });
  },

  methods: {
    headerBgCheck: function(){
      EventBus.$emit('headerBgChange', 'headerChangeInit' );
    },

    handleScroll: function(){
      let sT =  (document.documentElement && document.documentElement.scrollTop) || document.body.scrollTop;
      let sections = document.querySelectorAll( ".section" );
      let headerHeight = document.querySelector( "header" ).clientHeight;
      Object.entries(sections);
      
      sections.forEach( function( $ele, $index ){
        if( sT >= ($ele.offsetTop - headerHeight) ){
          $ele.classList.add( "fixed" );
        }else{
          $ele.classList.remove( "fixed" );
        }
      });

    },
    handelResize: function(){
      const wW = window.innerWidth;
      const responseMinsize = 960;
      if( wW <= responseMinsize){
        console.log( " " );
      }
    },

    section1Show: function(){
      let section1 = document.getElementById( "section01" );
      let section1Img = document.querySelector( ".img_wrap" );
      let headerHeight = document.querySelector( "header" ).clientHeight;
      let imgEffect = document.querySelector( ".img-effect" );
      let imgEffectImg = document.querySelector( ".img-effect > img" );


      let tl = gsap.timeline({
        scrollTrigger: {
          trigger: section1,
          // scrub: true, 
          // pin: true,
          start : "top top",
          end: "+=" + headerHeight / 2,
        }
      });

      tl.set( section1Img,{ marginLeft: 60+ "%" })
      tl.to( section1Img, 0.1, { marginLeft: 20+ "%", ease:Expo.easeInOut });

      let section1Box2 = document.querySelector( ".section1 .box02" );
      let t2 = gsap.timeline({
        scrollTrigger: {
          trigger: section1Box2,
          // endTrigger: section1Img,
          // scrub: true, 
          // pin: true,
          // markers: true,
          start : "top +=800",
          end: "+=1500",
        }
      });

      t2.set(imgEffect, { autoAlpha: 1 });
      t2.from(imgEffect, 1.5, {
        xPercent: -100,
        ease: Power2.out
      });
      t2.from(imgEffectImg, 1.5, {
        xPercent: 100,
        scale: 1.3,
        delay: -1.5,
        ease: Power2.out
      });
    },

    section2Show: function(){
      console.log( "section2  show" );
    },

    section3Show: function(){
      console.log( "section3 show" );
    }


  }
}
</script>

