          opacity: 1;
        }

        #section01 .contents_right .box01 {
          .img_wrap, .text_wrap {
            opacity: 1;
          }
        }
      }

      #section01 {
        position: relative;
        z-index: 1;

        .contents_right {
          padding-top: 0px;

          &::before {
            content: "";
            height: 450px;
            background: #323232;
            width: 100%;
            display: block;
          }

          .inner {
            margin-top: -18%;

            > div {
              margin-bottom: 22%;
            }
          }

          .text_wrap {
            .text01 {
              font-size: 21px;
              font-weight: 600;
              margin-bottom: 20px;
            }

            .text02 {
              font-size: 52px;
              line-height: 65px;
              font-weight: 300;
              margin-bottom: 40px;
            }

            .text03 {
              font-size: 16px;
              line-height: 28px;
              font-weight: 300;
            }
          }

          .box01 .text_wrap, .box03 .text_wrap {
            position: absolute;
            left: 0;
            bottom: -16%;
          }

          .box01 .img_wrap, .box03 .img_wrap {
            width: 80%;
            margin-left: 60%;
          }

          .box02 {
            margin-right: 3%;

            .img_wrap {
              display: inline-block;
              width: 45%;
              margin-right: 8%;
              vertical-align: middle;
                &.img-effect{    
                    position: relative;
                    height: rem(408px);
                    max-width: rem(620px);
                    overflow: hidden;
                img{
                  height: 100%;
                  width: 100%;
                  -o-object-fit: cover;
                  object-fit: cover;
                  -webkit-transform-origin: left;
                  transform-origin: left;
                }
              }
            }

            .text_wrap {
              display: inline-block;
              width: 47%;
              vertical-align: middle;
              margin-top: 4%;
            }
          }
          .box03{ position: relative; }
        }
      }

      #section02 {
        position: relative;
        z-index: 2;

        .contents_right {
          .inner {
            position: relative;
          }

          .text_wrap_slide {
            display: inline-block;
            width: 50%;
            vertical-align: middle;
            margin-top: 10%;
          }

          .text_wrap { 
            margin-bottom: 200px;

            .text01 {
              font-size: 90px;
              font-weight: 300;
              margin-bottom: 20px;
            }

            .text02 {
              font-size: 16px;
              line-height: 28px;
              font-weight: 300;
              margin-bottom: 110px;
            }

            .text03 {
              font-size: 30px;
              line-height: 55px;
            }
          }
        }

        &.fixed .contents_right .img_wrap_slide {
          position: fixed;
          left: 65%;
          top: 15%;
          display: block;
        }

        .contents_right {
          .img_wrap_slide .img_bg {
            width: 343px;
            height: 672px;
            position: relative;
            background: url(  ../assets/images/about/cp_mobile_bg.png);
          }

          .img_wrap {
            /*opacity: 0; visibility: hidden; */

            position: absolute;
            width: 289px;
            top: 57px;
            left: 14px;
            -khtml-transition: all 0.5s ease;
            -moz-transition: all 0.5s ease;
            -ms-transition: all 0.5s ease;
            -o-transition: all 0.5s ease;
            transition: all 0.5s ease;

            img {}
          }

          .img_box {
            position: absolute;

            .text_wrap {
              display: none;
            }
          }
        }
      }

      #section03 {
        position: relative;
        z-index: 3;

        .contents_right {
          .inner {
            padding-left: 0%;
          }

          .client_inner {
            width: 500px;

            ul {
              width: auto;

              li {
                text-align: center;
                width: 25%;
                border: 1px solid #efefef;
                box-sizing: border-box;
                margin-right: -1px;
                margin-bottom: -2px;
                padding: 3% 0;
                -khtml-transition: all 0.5s ease;
                -moz-transition: all 0.5s ease;
                -ms-transition: all 0.5s ease;
                -o-transition: all 0.5s ease;
                transition: all 0.5s ease;

                &:nth-of-type(4n) {
                  border-right: 1px solid #efefef;
                }

                img {
                  width: auto;
                  height: 53px;
                }

                &:nth-of-type(20) ~ li {
                  display: none;
                }
              }
            }
          }
        }
      }

      #section04 {
        position: relative;
        z-index: 4;

        .contents_right {
          .inner {
            padding-left: 0%;
            font-size: 0;
          }

          .contact_info {
            padding-left: 7%;
            margin-bottom: 7%;

            dt, dd {
              font-size: 16px;
              line-height: 35px;
            }

            dt {
              width: 150px;
              font-weight: 600;
            }

            dd {
              margin: 0;
            }
          }

          .write_btn {
            margin-top: 30px;
          }
        }
      }
    }

    /*     */

    /* ABOUT US */

    /* SERVICE */

    /* CLIENT */

    /* CONTACT US */

    /***** /  *****/

    @media (max-width: 1280px) {
      .contents_left .text02 {
        display: block;
      }

      /**  **/

      .company_wrap {
        .section {
          display: block;

          .contents_left {
            width: 100%;
            display: block;
            padding: 0;
          }
        }

        #section01 .contents_left {
          padding: 63px 0 0 0;
        }

        .section {
          .contents_left .inner {
            position: static ! important;
            width: 100% ! important;
            padding: 5% 5%;
            box-sizing: border-box;
          }

          &.fixed .contents_left .inner .inner_wrapper {
            position: static;
          }

          .contents_left {
            .text01 {
              font-size: 42px;
              line-height: 48px;
              padding: 0;
              margin: 0 0 15px 0;
              width: 280px;
              border: none;

              &:after {
                display: none;
              }

              br {
                display: noen;
              }
            }

            .text02 br {
              display: block;
              font-size: 12px;
            }

            .navi_wrap {
              display: none;
            }
          }

          .contents_right {
            padding: 0;
            width: 100%;
            display: block;

            .title_wrap {
              padding: 5% 5%;
              margin-bottom: 0;

              .text01 {
                font-size: 15.7px;
                margin-bottom: 35px;
              }

              .text02 {
                font-size: 31.5px;
                line-height: 39px;
              }
            }
          }
        }

        #section01 .contents_right {
          display: block;
          width: 100%;
          margin-top: 7%;

          &::before {
            display: none;
          }

          .inner {
            margin-top: 0;
            padding: 0 5%;

            > div {
              margin-bottom: 7%;
            }
          }

          .text_wrap {
            .text01 {
              font-size: 15.7px;
            }

            .text02 {
              font-size: 39px;
              line-height: 49px;
              margin-bottom: 30px;
            }
          }

          .box01 .text_wrap, .box03 .text_wrap {
            position: static;
            margin-top: 5%;
          }

          .box01 .img_wrap, .box03 .img_wrap {
            width: 100%;
            margin-left: 0 ! important;
          }

          .box02 { 
            .img_wrap {
              width: 100%;
              margin-right: 0;
            }

            .text_wrap {
              width: 100%;
              margin-top: 5% ! important;
            }
          }
        }

        #section02 .contents_right {
          .inner {
            padding: 0;

            .inline {
              padding: 7% 5%;
            }
          }

          .text_wrap_slide {
            display: none;
          }

          .text_wrap {
            margin-bottom: 100px;

            .text01 {
              font-size: 54px;
              margin-bottom: 17px;
            }

            .text02 {
              font-size: 17px;
              line-height: 25px;
              margin-bottom: 45px;

              br {
                display: none;
              }
            }

            .text03 {
              font-size: 22px;
              line-height: 31px;
            }
          }

          .img_wrap_slide {
            padding: 15% 0;
            width: 100%;
          }

          .img_wrap {
            text-align: center;
            padding-top: 0;
            position: static;
          }

          .img_wrap_slide .img_bg {
            background: none;
            width: 300px;
            height: auto;
            max-width: 100%;
          }

          .img_wrap {
            width: 100%;
            box-sizing: border-box;
            background: url( ../assets/images/about/cp_mobile_bg.png) no-repeat;
            padding: 51px 23px;
            background-size: 100%;
            background-position: 13px -1px;
          }

          .img_box {
            position: static;
            padding: 0 10%;
            box-sizing: border-box;

            .text_wrap {
              display: block;
              min-height: 300px;
            }
          }
        }

        #section03 .contents_right {
          margin-top: 0;

          .client_inner ul {
            border-bottom: 1px solid #efefef;

            li {
              width: 33.33%;
              margin-right: 0;
              margin-bottom: -1px;
              margin-top: 0;
              border-right: none;

              img {
                height: 38px;
                max-width: 100%;
              }

              &:nth-of-type(4n) {
                border-right: none;
              }

              &:last-child {
                border-right: 1px solid #e6e6e6;
                display: none;
              }
            }
          }
        }

        #section04 .contents_right .contact_info {
          padding: 0 5%;
          margin-bottom: 7%;

          dt {
            font-size: 12px;
            width: 30%;
          }

          dd {
            font-size: 12px;
            width: 70%;
          }
        }
      }

      /* ABOUT US */

      /* SERVICE */

      /* CLIENT */

      /* CONTACT US */
    }

    @media (max-width: 769px) {
      /**  **/

      .company_wrap {
        .section {
          .contents_left .inner {
            padding: 15% 5%;
          }

          .contents_right {
            margin-top: 0px;

            .title_wrap {
              padding: 15% 5%;
            }
          }
        }

        #section01 .contents_right {
          margin-top: 10%;

          .inner > div {
            margin-bottom: 15%;
          }
        }

        #section02 .contents_right {
          .text_wrap_slide {
            width: 100%;
            display: none;
          }

          .text_wrap {
            margin-bottom: 50px;

            .text01 {
              font-size: 47.5px;
              margin-bottom: 17px;
            }

            .text02 {
              font-size: 12px;
              line-height: 21px;
            }

            .text03 {
              font-size: 18.5px;
              line-height: 31px;
            }
          }

          .img_box {
            .text_wrap {
              min-height: 250px;
            }

            padding: 0 5%;
          }
        }

        #section03 .contents_right {
          margin-top: 40px;

          .client_inner ul li img {
            height: 30px;
          }
        }

        #section04 .contents_right .contact_info {
          margin-bottom: 15%;
        }
      }

      /* ABOUT US */

      /* SERVICE */

      /* CLIENT */

      /* CONTACT US */
    }
  }

// font-size: 118px;
//     font-weight: 500;
//     letter-spacing: -5px;
//     text-transform: uppercase;
//     line-height: 132px;
// }

</style>


